.overlay.defense {
  -ms-flex-pack: end;
      justify-content: flex-end;
  padding-bottom: 1em;
  background-color: transparent;
}

.overlay.defense-right {
  -ms-flex-align: end;
      align-items: flex-end;
  padding-right: 6em;
  text-align: right;
  background-image: -webkit-gradient(linear, left top, right top, color-stop(50%, rgba(0, 0, 0, 0)), color-stop(50%, #000));
  background-image: -webkit-linear-gradient(left, rgba(0, 0, 0, 0) 50%, #000 50%);
  background-image: -o-linear-gradient(left, rgba(0, 0, 0, 0) 50%, #000 50%);
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0) 50%, #000 50%);
}

.overlay.defense-left {
  -ms-flex-align: start;
      align-items: flex-start;
  padding-left: 6em;
  text-align: left;
  background-image: -webkit-gradient(linear, right top, left top, color-stop(50%, rgba(0, 0, 0, 0)), color-stop(50%, #000));
  background-image: -webkit-linear-gradient(right, rgba(0, 0, 0, 0) 50%, #000 50%);
  background-image: -o-linear-gradient(right, rgba(0, 0, 0, 0) 50%, #000 50%);
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0) 50%, #000 50%);
}

.defense-explanation p {
  margin-bottom: 0;
}

.defense-options {
  position: absolute;
  top: 1em;
  bottom: 1em;
  width: 4em;
}

.defense-right .defense-options {
  right: 1em;
}

.defense-left .defense-options {
  left: 1em;
}

.defense-options div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  position: absolute;
  left: 0;
  right: 0;
  height: calc(50% - 0.5em);
  color: #000;
  background-color: #fff;
  cursor: pointer;
}

.defense-options .defense-top {
  top: 0;
}

.defense-options .defense-bottom {
  bottom: 0;
}



