.player {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 12em;
  color: #000;
  background-color: #fff;
  text-align: center;
}

.player.is-attacking {
  -webkit-animation: blink 2s steps(1) infinite;
          animation: blink 2s steps(1) infinite;
}

.score {
  font-size: 4em;
  line-height: 1.125;
}

.douple-ups {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 5em;
  height: 1em;
  margin-top: 1.5em;
}

.douple-up {
  width: 1em;
  height: 1em;
  background-color: currentColor;
}

@-webkit-keyframes blink {
  0%,
  30%,
  51% {
    color: #000;
  }

  31%,
  50% {
    color: #fff;
  }
}

@keyframes blink {
  0%,
  30%,
  51% {
    color: #000;
  }

  31%,
  50% {
    color: #fff;
  }
}



