.paddle {
  position: absolute;
  top: 38vh;
  width: 1em;
  height: 20vh;
  background-color: #fff;
  -webkit-transition: top 1s linear;
  -o-transition: top 1s linear;
  transition: top 1s linear;
  will-change: top;
}

.paddle-left {
  left: 1em;
}

.paddle-right {
  right: 1em;
}

/* stylelint-disable */

.defend-top-0 .is-defending { top: 20vh; -webkit-transition-delay: 1s; -o-transition-delay: 1s; transition-delay: 1s; }
.defend-top-1 .is-defending { top: 5vh; -webkit-transition-delay: 3s; -o-transition-delay: 3s; transition-delay: 3s; }
.defend-top-2 .is-defending { top: 5vh; -webkit-transition-delay: 1s; -o-transition-delay: 1s; transition-delay: 1s; }
.defend-top-3 .is-defending { top: 20vh; -webkit-transition-delay: 0.5s; -o-transition-delay: 0.5s; transition-delay: 0.5s; }
.defend-top-4 .is-defending { top: 5vh; -webkit-transition-delay: 1.5s; -o-transition-delay: 1.5s; transition-delay: 1.5s; }

.defend-bottom-0 .is-defending { top: 55vh; -webkit-transition-delay: 1s; -o-transition-delay: 1s; transition-delay: 1s; }
.defend-bottom-1 .is-defending { top: 75vh; -webkit-transition-delay: 3s; -o-transition-delay: 3s; transition-delay: 3s; }
.defend-bottom-2 .is-defending { top: 75vh; -webkit-transition-delay: 1s; -o-transition-delay: 1s; transition-delay: 1s; }
.defend-bottom-3 .is-defending { top: 55vh; -webkit-transition-delay: 0.5s; -o-transition-delay: 0.5s; transition-delay: 0.5s; }
.defend-bottom-4 .is-defending { top: 75vh; -webkit-transition-delay: 1.5s; -o-transition-delay: 1.5s; transition-delay: 1.5s; }

/* stylelint-enable */



